<script>
import NavData from '../assets/Data.json'

export default {
  name: "NavPage",
  data() {
    return {
      NavSelectIndex: null,
      Btnindex:0,
      // BtnSideimg:["../assets/images/button/btn_submenu_up.png", "../assets/images/button/btn_submenu_down.png"],
      SideMenuDown: false,
      SideMenuDown2: false,
      SideMenuOpen: false,
      MenuOpen: false,
      windowWidth : 0, // 윈도우 가로길이의 초기 값
      Nav: NavData.Nav,
      Lnb: NavData.Lnb,
      SubMenu: NavData.SubMenu,
      SubList: NavData.SubList,
      SubMenuMd:NavData.SubMenuMd,
      Etc : NavData.Etc

    }
  },
  // ↓↓↓↓↓↓ 실시간으로 가로 길이 값을 체크해서 페이지 너비에 맞는(pc / mobile) 메뉴바 출력 ↓↓↓↓↓↓
  methods:{ 
    updateWindowWidth(){
      this.windowWidth = window.innerWidth;
    }
  },
  computed:{
    isMobile(){
      return this.windowWidth <= 985;
      // 가로 길이 값이 설정한 값 보다 작으면 해당 메소드의 함수 실행
    }
  },
  mounted(){
    this.updateWindowWidth();
    window.addEventListener('resize', this.updateWindowWidth);
    // 페이지 로딩 시 실시간으로 체크 'resize'
  }
  // ↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑↑ 

}
</script>
